:root {
  --indent: 20px;
  --header-height: 100px;
}

html,
body {
  height: 100%;
}

body {
  min-width: 320px;
  margin: 0;
  line-height: 1.3;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  color: #3e1f05;
  text-size-adjust: 100%;
}

h1,
h2,
h3,
h4 {
  font-weight: 600;
  font-family: "Roboto Condensed", Arial, Helvetica, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  object-fit: contain;
}

a:not(.button) {
  transition: opacity 0.25s ease-in-out;
}

a:not(.button):hover {
  opacity: 0.8;
}

a:not(.button):active {
  opacity: 0.6;
}

section {
  padding-top: 100px;
  padding-bottom: 100px;
}

.visually-hidden {
  position: absolute;
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
  overflow: hidden;
  border: none;
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1000px;
  margin: 0 auto;
  padding: var(--indent);
}

.logo {
  display: block;
}

.logo img {
  display: block;
}

.button {
  display: inline-block;
  padding: 12px 24px;
  font-weight: 600;
  font-size: 16px;
  line-height: 125%;
  font-family: inherit;
  text-align: center;
  color: #fdfdfd;
  text-decoration: none;
  letter-spacing: 0.192px;
  background-color: #1A3E3E;
  background-image: linear-gradient(135deg, #3e1f05, #6a4e26, #c29c63, #d4b383);
  border: 0;
  border-radius: 4px;
  box-shadow: 0 3px 0 #6a4e26, 0 0 90px #ffffff;
  transition: box-shadow 0.25s ease-in-out, opacity 0.25s ease-in-out;
  appearance: none;
}

.button:not(:disabled):hover {
  box-shadow: 0 0 0 #6a4e26, 0 0 90px #ffffff;
  cursor: pointer;
}

.button:not(:disabled):active {
  opacity: 0.7;
}

.button::before {
  content: "";
  display: inline-block;
  height: 1em;
  vertical-align: middle;
}

.icon-link {
  display: inline-flex;
  align-items: center;
  color: inherit;
  text-transform: uppercase;
  text-decoration: none;
}

.icon-link::before {
  content: "";
  display: block;
  width: 20px;
  height: 20px;
  margin-right: 10px;
  background-image: linear-gradient(135deg, #3e1f05, #6a4e26, #c29c63, #d4b383);
  mask-position: center;
  mask-size: contain;
  mask-repeat: no-repeat;
}

.icon-link--telegramm::before {
  mask-image: url(../../img/telegram.svg);
}

.title {
  font-size: 1.5em;
  text-transform: uppercase;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: var(--header-height);
  color: #fff;
}

.header__container {
  display: flex;
  align-items: center;
}

.header__link {
  margin-left: auto;
}

.main {
  overflow: hidden;
}

.hero {
  position: relative;
  padding-top: var(--header-height);
  padding-bottom: 0;
}

.hero::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: -2;
  width: 100%;
  height: 100%;
  background-image: url(../../img/hero-bg.jpg);
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
}

.hero::after {
  content: "";
  position: absolute;
  bottom: -1px;
  left: 0;
  z-index: -1;
  width: 50%;
  padding-top: 25vw;
  background-image: url(../../img/trangle-left.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 100% auto;
}

.hero__container::after {
  content: "";
  position: absolute;
  right: 0;
  bottom: -1px;
  z-index: -2;
  width: 50vw;
  padding-top: 25vw;
  background-image: url(../../img/trangle-right.svg);
  background-repeat: no-repeat;
  background-position: right bottom;
  background-size: 100% auto;
}

.hero__title {
  position: relative;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 290px;
  margin: 0 auto;
  padding: 50px 0 90px;
  letter-spacing: 2px;
  text-shadow: -1px -1px 0 rgba(255, 255, 255, 0.6), 1px -1px 0 rgba(255, 255, 255, 0.6), -1px 1px 0 rgba(255, 255, 255, 0.6), 1px 1px 0 rgba(255, 255, 255, 0.6);
  text-wrap-style: balance;
}

.hero__title::before {
  content: "";
  position: absolute;
  top: 65%;
  left: 50%;
  z-index: -1;
  width: 110vw;
  max-width: 400px;
  background-image: url(../../img/hero-img.png);
  background-repeat: no-repeat;
  background-size: contain;
  transform: translate(-50%, -50%);
  aspect-ratio: 1/1;
}

.hero__button {
  display: block;
  margin: 0 auto;
}

@media (min-width: 768px) {
  .button {
    padding: 24px 48px;
    font-size: 18px;
    border-radius: 6px;
  }

  .hero {
    padding-top: calc(var(--header-height) + 100px);
  }

  .hero__container {
    padding-bottom: 50px;
  }

  .hero__title {
    width: 450px;
    font-size: 2.4em;
  }

  .hero__title::before {
    max-width: 130%;
  }
}

@media (min-width: 1000px) {
  .hero {
    padding-top: calc(var(--header-height) + 200px);
  }

  .hero__container {
    padding-bottom: 150px;
  }

  .hero__title {
    margin-left: 0;
    font-size: 2.8em;
  }

  .hero__title::before {
    transform: translate(-30%, -50%);
    max-width: 200%;
  }
}